// fingo - nav.sass , header.sass
//
// * --------------------------------
// * RESET
// * --------------------------------
@import "./config/fingo-colors";
@import "./Modules/sass/style";


input, .nav {
  @extend %reset-border-box; }
input {
  @extend %reset-input; }
// reset
button {
  @extend %reset-button; }

// .account, .total-movies, .received-likes
//// @extend %position-center

body {
  font-family: 'Spoqa Han Sans', 'Sans-serif';
  @include size(100% auto);
  @include background(color $fingo-bg-light); }

.container {
  @include size(1240px auto);
  @include margin(0 auto);
  @include tablet {
    width: percentage($t-container-width/$bp-tablet); }
  @include mobile {
    width: percentage($m-container-width/$bp-mobile); } }



// * --------------------------------
// * Nav
// * --------------------------------
.nav-active {
  @include background(color $fingo-bg-dark); }
.nav {
  position: fixed;
  @include size(100% 100px);
  z-index: 100;

  .logo {
    position: relative;
    >h1 {
      display: inline-block;
      @include margin(top 29px);
      span {
        @extend %a11y-hidden; } }
    .main-menu-btn {
      display: none;
      @include tablet {
        display: block;
        @include position(absolute, top 40px right 0px);
        z-index: 30; }

      @include mobile {
        display: block;
        @include position(absolute, top 40px right 0px);
        z-index: 30; }

      button {
        @include size(100% auto);
        span {
          display: block;
          @include size(30px 1px);
          @include background(color $fingo-white);
          @include margin(bottom 5px);
          transition: all 0.3s; } } }
    .main-menu-btn-active span:nth-child(2),
    .main-menu-btn-active span:nth-child(3) {
      opacity: 0; }
    .main-menu-btn-active span:nth-child(1) {
      transform: translateY(10px) rotate(45deg) scale(1.4); }
    .main-menu-btn-active span:nth-child(4) {
      transform: translateY(-8px) rotate(-45deg) scale(1.4); } }




  ul {
    @include position(absolute, top 0 left 135px);
    z-index: 20;
    transition: 0.5s all;
    @include tablet {
      @include size(120% 450px);
      @include position(absolute, top -450px left -10%);
      text-align: center;
      background: rgba(#1e1e1e, 0.9); }

    @include mobile {
      @include size(120% 450px);
      @include position(absolute, top -450px left -10%);
      text-align: center;
      background: rgba(#1e1e1e, 0.9); }



    li {
      display: inline-block;
      @include tablet {
        display: block;
        @include position(relative, top 80px); }
      @include mobile {
        display: block;
        @include position(relative, top 80px); }

      a {
        display: line-block;
        font-size: 16px;
        @include margin(right 40px);
        color: $fingo-white;
        line-height: 100px;
        position: relative;
        transition: all 0.3s;
        font-weight: normal;
        @include tablet {
          font-size: 25px;
          @include margin(right 0px);
          font-weight: lighter; }
        @include mobile {
          font-size: 25px;
          @include margin(right 0px);
          font-weight: lighter; }
        &:hover {
          @include tablet {
            color: $fingo-mian-color; }
          @include mobile {
            color: $fingo-mian-color; } }
        &::after {
          content: "";
          display: block;
          @include size(100% 2px);
          @include background(color $fingo-mian-color);
          @include position(absolute,top 20px left 0px);
          transition: all 0.3s;
          transform: scaleX(0);
          @include tablet {
            display: none; }
          @include mobile {
            display: none; } }


        &:hover::after {
          transform: scaleX(1); } } } }
  .main-menu-active {
    @include position(absolute, top 0px left -10%); }




  form {
    @include position(absolute, top 33px right 133px);
    @include tablet {
      @include position(absolute, top 33px right 200px); }
    @include mobile {
      @include position(absolute, top 33px right 155px); }
    label {
      @extend %a11y-hidden; }
    input {
      @include size(350px 35px);
      border: 1px solid $fingo-border;
      transition: 0.3s all;
      &:hover,
      &:focus {
        border: 1px solid #ddd; }
      @include tablet {
        @include size(300px 35px); }
      @include mobile {
        @include size(200px 35px); }
      @include padding(left 20px);
      @include background(color rgba(#161616, 0.5));
      border-radius: 50px;
      color: $fingo-font-dd;
      position: relative; }
    button {
      @include position(absolute, top 7px right 20px); } }

  .profile-menu {
    @include position(absolute, top 28px right 0);
    @include tablet {
      @include position(absolute, top 33px right 70px); }
    @include mobile {
      @include position(absolute, top 33px right 50px); }
    &::after {
      content: "";
      display: block;
      @include size(100% 2px);
      @include background(color $fingo-mian-color);
      @include position(absolute,top 50px left 0px);
      transition: all 0.3s;
      transform: scaleX(0); }
    &:hover::after {
      transform: scaleX(1); }
    h1 {
      @extend %a11y-hidden; }
    p {
      display: inline-block;
      line-height: 42px;
      color: $fingo-white;
      @include margin(right 15px);
      font-style: 16px; }
    img {
      @include size(42px 42px); } } }

// * --------------------------------
// * Header
// * --------------------------------
header {
  @include size(100% 450px);
  color: #fff;
  @include background(image url('../images/sub-bg-img.png') position center center repeat no-repeat size cover);
  .profile {
    height: 450px;
    text-align: center;
    .account {
      position: relative;
      // display: inline-block
      @include padding(top 200px);
      text-align: center;
      h1 {
        @extend %a11y-hidden; }
      .thumbnail {
        img {
          @include size(75px 75px); } }
      p {
        font-size: 18px;
        @include margin(top 10px); } }
    section:nth-child(2), section:nth-child(3) {
      display: inline-block; }
    .total-movies {
      @include position(relative, top -95px left -50px);
      // +margin(top -80px  left 200px)
      h1 {
        @include position(relative, top 40px left 0);
        font-size: 12px; }
      strong {
        font-size: 16px;
        font-weight: normal; } }

    .received-likes {
      @include position(relative, top -95px right -50px);
      // +margin(top -48px  right 200px)
      h1 {
        @include position(relative, top 40px left 0);
        font-size: 12px; }
      img {
        @include position(relative, top 5px left 0); }
      strong {
        font-size: 16px;
        font-weight: normal; } } } }
// * --------------------------------
// * Main
// * --------------------------------
main {
  .sub-menu {
    @include size(100% 60px);
    border-bottom: 1px solid $fingo-border;
    ul {
      li {
        float: left;
        border-left: 1px solid $fingo-border;
        border-right: 1px solid $fingo-border;

        &:nth-child(2), &:nth-child(3), &:nth-child(4) {
          border-left: none; }

        &::after {
          content: "";
          display: block;
          @include size(100% 1px);
          @include background(color $fingo-mian-color);
          clear: both;
          transform: scale(0);
          transition: all 0.3s; }
        &:nth-child(3)::after {
          transform: scale(1); }
        &:hover::after {
          transform: scale(1); }

        a {
          @include size(auto 60px);
          @include padding(left 20px right 20px);
          display: block;
          color: $fingo-font-dd;
          font-size: 14px;
          line-height: 60px; } } } } }

.movies {
  @include margin(top 60px);


  h1 {
    @extend %a11y-hidden; }
  .movie-list {
    @include tablet {
      @include size(736px auto);
      @include margin(0 auto); }
    @include mobile {
      @include size(350px auto);
      @include margin(0 auto); }
    &::after {
      content: "";
      display: block;
      clear: both; }
    figure {
      @include margin(0);
      float: left;
      @include margin(right 20px bottom 20px);
      transition: 0.3s all;
      position: relative;
      &:nth-child(5n) {
        @include margin(right 0px); }
      @include tablet {
        &:nth-child(5n) {
          @include margin(right 20px); }
        &:nth-child(3n) {
          @include margin(right 0px); } }
      @include mobile {
        background-color: pink;
        // +margin(right 20px auto)
        @include size(350px 500px);
        &:nth-child(2n) {
          @include margin(right 0px); }
        &:nth-child(5n) {
          @include margin(right 20px); } }
      img {
        @include size(220px 320px);
        @include mobile {
          @include size(350px 500px); } }

      .hover {
        @include position(absolute, top 0 z-index 10);
        @include size(100% 100%);
        opacity: 1;
        cursor: pointer;
        background: rgba(#000000, 0.3);
        text-align: center;
        p {
          @include margin(top 55px);
          font-size: 16px;
          color: $fingo-white;
          @include mobile {
            font-size: 20px;
            @include margin(top 90px); } }


        .like-btn,
        .heart-btn {
          @include margin(bottom 10px left auto right auto);
          @include size(110px 35px);
          font-size: 14px;
          display: block;
          color: #fff;
          border: 1px solid currentColor;
          letter-spacing: -1px;
          @include mobile {
            @include size(180px 50px);
            font-size: 16px; }
          img:first-child {
            @include size(16px 13px);
            @include margin(right 5px);
            @include mobile {
              @include size(21px 19px); } }



          &:nth-of-type(1):hover,
          &:nth-of-type(1):active {
            @include background(color $fingo-heart);
            border: 1px solid $fingo-heart; }
          &:nth-of-type(2):hover,
          &:nth-of-type(2):active {
            @include background(color #27569c);
            border: 1px solid #27569c; } } }

      &:hover {
        .hover {
          opacity: 1; } } } } }
